<template>
  <v-navigation-drawer  v-model="drawer" dark app>
    <v-list elevation="12" nav>
      <v-list-item-group v-model="listModel">
        <v-list-item :disabled="item.disabled" :value="item.route" :key="index" v-for="(item,index) in list">
          <v-list-item-icon>
            <v-icon v-html="`fa-${item.icon}`"></v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{item.name}}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>

    <template v-slot:append>
      <div class="pa-2">
        <v-btn :loading="loading" @click="logout" rounded block>
          <v-icon>mdi-logout</v-icon> Çıxış
        </v-btn>
      </div>
    </template>
  </v-navigation-drawer>
</template>

<script>

import {logout} from '../utils/auth'

export default {
  props:{
    drawer:{
      default:false,
      type:Boolean
    }
  },
  data:() => ({
    loading:false,
    listModel:null,
    list:[
      {
        name : "Əsas",
        icon:'home',
        route:'dashboard'
      },
      {
        name : "Növbələr",
        icon:'user-clock',
        route:'queues'
      },
      {
        name : "Fəaliyyətlər",
        icon:'sitemap',
        route:'',
        disabled:true
      },
      {
        name : "Abunəliklər",
        icon:'clock-outline',
        route:'',
        disabled:true
      },
      {
        name : "Tənzimləmələr",
        icon:'cog',
        route:'',
        disabled:true
      }
    ]
  }),
  mounted() {
    this.listModel = this.$route.name;
  },
  methods:{
    async logout() {
      this.loading = true;
      await logout()
      this.loading = false;
    }
  },
  watch:{
    listModel (v) {
      if (this.$route.name !== v) {
        this.$router.push({name:v})
      }
    }
  }
}
</script>